.links {
  display: block;
  margin-top: 52px;
  margin-right: 10px;
  width: 150px;
  font-size: 12.5px;
  min-width: 150px;
}

.item {
  display: flex;
  align-items: center;
  padding: 0 5px;
  height: 28px;
  cursor: pointer;
  color: #285473;
  transition: font-weight 0.1s linear;
  text-decoration: none;
  font-size: 14px;
  white-space: nowrap;
}

.item i {
  font-size: 16px;
  margin-right: 8px;
  font-weight: 500;
}

.item:hover {
  background-color: rgba(81, 129, 184, 0.1);
  border-radius: 6px;
}

a.active {
  font-weight: bold;
}

a.active i {
  font-weight: bold;
}

@media (max-width: 414px) {
  .links {
    display: flex;
    align-items: center;
    justify-content: space-around;
    position: fixed;
    width: 100vw;
    height: 48px;
    bottom: 0;
    left: 0;
    background: #4a76a8;
    z-index: 1000;
  }

  .links div {
    display: none;
  }

  .links i {
    font-size: 25px;
    color: #edeef0;
  }
}
