.Profile {
  display: flex;
  width: 100%;
}

.col1 {
  display: block;
  min-width: 230px;
  max-width: 230px;
  margin-right: 10px;
}

.avatar {
  background: white;
  border: 1px solid #d7d8db;
  border-radius: 5px;
  text-align: center;
  height: auto;
}

.avatar img {
  width: 85%;
  padding: 10px;
}

.col2 {
  display: block;
  width: 100%;
}


@media (max-width: 414px) {
  .Profile {
    flex-direction: column;
    justify-content: center;
  }

  .col1 {
    margin: 0 auto;
    min-width: 80%;
    max-width: 80%;
    margin-bottom: 20px;
  }
}