.info {
  background: white;
  border: 1px solid #d7d8db;
  border-radius: 5px;
  color: #3f3f3f;
  height: 210px;
  padding: 10px;
  flex-wrap: wrap;
  position: relative;
}

.preloader {
  display: block;
  height: 100%;
  margin: 0 auto;
}

.name {
  font-size: 25px;
  color: #3f3e46;
}

.about {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.info_item {
  position: relative;
}

.edit {
  position: absolute;
  right: 10px;
  top: 0;
  cursor: pointer;
  color: gray;
}

.edit:hover {
  color: black;
}

.ProfileFormData {
  position: absolute;
  background: white;
  border-radius: 10px;
  border: 1px solid gray;
  width: 400px;
  padding: 5px;
  z-index: 1;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.5);
  animation: animatetop 0.5s;
}

@keyframes animatetop {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.ProfileFormData input {
  border: none;
  border-bottom: 1px solid gray;
}

.save {
  position: absolute;
  right: 15px;
  top: 5px;
  font-size: 30px;
  color: gray;
  cursor: pointer;
  border: none;
  background: none;
}

.save:hover {
  color: black;
}

.error {
  color: red;
  position: absolute;
  bottom: 5px;
  right: 5px;
  font-size: 14px;
}
