.box__avatar {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px;
  background: white;
  border: 1px solid #d7d8db;
  border-radius: 5px;
  width: 100%;
  min-height: 200px;
  height: auto;
}

.box__avatar img {
  width: 100%;
  border-radius: 3px;
}

.box__button {
  cursor: pointer;
  margin-top: 5px;
  overflow: hidden;
  position: relative;
}

.file {
  position: absolute;
  height: 30px;
  opacity: 0;
}

.button {
  border-radius: 5px;
  border: none;
  background: #4a76a8;
  color: white;
  font-weight: bold;
  height: 30px;
  width: 100%;
  z-index: -1;
}
