.header {
  display: flex;
  align-items: center;
  width: 100%;
  background: #4a76a8;
  position: fixed;
  top: 0;
  color: white;
  font-size: 30px;
  z-index: 1000;
}

.header a {
  color: white;
  font-size: 20px;
}

.logout {
  right: 2px;
  top: 5px;
  background: none;
  border: none;
  font-size: 20px;
  color: white;
}

.logout:hover {
  color: #edeef0;
  cursor: pointer;
}


@media (max-width: 414px) {
  .header {
    height: 56px;
  }
}